.project {
  flex-direction: column;
  padding-bottom: 60px;

  @media (width <=833px) {
    padding-bottom: 30px;
  }

  &__title {
    margin: 0 auto;
    margin-bottom: 20px;
    padding-top: 20px;
    font-weight: 900;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--text-color);
  }

  &__filter {
    margin-bottom: 30px;
  }

  &__list {
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;

    @media (width <=661px) {
      flex-wrap: nowrap;
      justify-content: flex-start;
      overflow: scroll;
    }
  }

  &__btn {
    outline: none;
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    @media (width <=661px) {
      min-width: max-content;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__card-list {
    flex-wrap: wrap;
    margin-bottom: 30px;
    gap: 30px;

    @media (width <=661px) {
      justify-content: center;
    }
  }

  &__card-item {
    outline: none;
    border: 2px solid var(--accent-color);
    border-radius: 8px;
    padding: 20px;
    width: 300px;
    color: var(--text-color);
    transition: all .3s ease-in-out;

    &:focus {
      border-color: var(--subaccent-color);

      .project__card-logo {
        border-color: var(--subaccent-color);
      }

      .project__card-category {
        border-color: var(--subaccent-color);
      }

      .project__card-descr {
        border-color: var(--subaccent-color);
      }
    }
  }

  &__card-logo {
    margin-bottom: 10px;
    border: 2px solid var(--accent-color);
    border-radius: 100%;
    width: 60px;
    height: 60px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all .3s ease-in-out;
  }

  &__card-name {
    margin-bottom: 10px;
    font-weight: 900;
    text-transform: uppercase;
  }

  &__card-category {
    margin-bottom: 10px;
    border: 2px solid var(--accent-color);
    border-radius: 8px;
    padding: 4px 6px 3px;
    max-width: fit-content;
    font-size: 10px;
    text-transform: uppercase;
    transition: all .3s ease-in-out;
  }

  &__card-descr {
    margin-bottom: 20px;
    border-bottom: 2px solid var(--accent-color);
    min-height: 110px;
    font-size: 12px;
    line-height: 2;
    transition: all .3s ease-in-out;
  }

  &__card-btn {
    align-self: center;
    outline: none;
    border-radius: 8px;
    padding: 20px 40px;
    max-width: 200px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }
  }

  &__social-icon {
    width: 24px;
    height: 24px;
    fill: var(--text-color);
  }

  &__social-item {
    outline: none;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: 2px solid var(--text-color);
    border-radius: 8px;
    padding: 8px;
    width: 100%;
    height: 100%;
    transition: all .3s ease-in-out;

    @include hover {
      border: 2px solid var(--subaccent-color);
      background-color: var(--subaccent-color);

      .project__social-icon {
        fill: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }

    &:focus {
      border: 2px solid var(--subaccent-color);
      background-color: var(--subaccent-color);

      .project__social-icon {
        fill: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }
  }
}
