.modal {
  position: fixed;
  left: 50%;
  top: 50%;
  display: none;
  flex-direction: column;
  border-radius: 8px;
  padding: 30px;
  width: 50%;
  background-color: var(--bg-color);
  transform: translate(-50%, -50%);

  &.active {
    display: block;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    color: var(--text-color);

    &-sm {
      margin-bottom: 60px;
      font-size: 16px;
      color: var(--subtext-color);
    }
  }

  &__list {
    justify-content: space-between;
    margin-bottom: 30px;
    width: 100%;
  }

  &__item {
    outline: none;
    border: 1px solid var(--subaccent-color);
    border-radius: 8px;
    padding: 10px 10px 6px;
    transition: all .3s ease-in-out;

    @include hover {
      background-color: var(--subaccent-color);

      .modal__icon {
        fill: var(--bg-color);
      }
    }

    &:focus {
      background-color: var(--subaccent-color);
    }

    &:focus .modal__icon {
      fill: var(--bg-color);
    }
  }

  &__link {
    outline: none;
    transition: all .3s ease-in-out;
  }

  &__icon {
    width: 20px;
    height: 20px;
    fill: var(--subaccent-color);
    transition: all .3s ease-in-out;

    &-close {
      width: 20px;
      height: 20px;
      stroke: var(--sutext-color);
    }
  }

  &__btn {
    outline: none;
    transition: all .3s ease-in-out;

    &-form {
      border-radius: 8px;
      padding: 11px 30px;
      max-width: fit-content;
      font-size: 14px;
      text-transform: uppercase;
      color: var(--bg-color);
      background-color: var(--subtext-color);
      cursor: not-allowed;

      &:focus {
        background-color: var(--subtext-color);
        cursor: not-allowed;
      }
    }

    &-close {
      position: absolute;
      right: 30px;
      top: 30px;
      border: 1px solid var(--subaccent-color);
      border-radius: 8px;
      padding: 10px 10px 6.3px;
      stroke: var(--subaccent-color);

      @include hover {
        background-color: var(--subaccent-color);

        .modal__icon-close {
          stroke: var(--bg-color);
          transition: all .3s ease-in-out;
        }
      }

      &:focus {
        background-color: var(--subaccent-color);
      }

      &:focus .modal__icon-close {
        stroke: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }
  }
}
